import React, { Component } from 'react';
import axios from 'axios';
import $ from 'jquery';
import {Checkbox, CheckboxGroup} from 'react-checkbox-group';
import ShowExercise from '../showExercise/showExercise.js'
import ShowWorkout from '../showWorkout/showWorkout.js';

import APIs from '../template/constants.js';

class CreateAll extends Component {
	constructor(props) {
		super(props);
		this.state = {
			userID:null,
			video_url:"",
			AllExercises:[],
			selectedExercise:[],
			selectedWorkout:[]
		};
	}
	componentWillMount(){
		this.setState({userID:localStorage.getItem('id'), AllExercises:this.props.AllExercises, AllWorkouts:this.props.AllWorkouts})
	}
	componentWillReceiveProps(nextProps) {
		console.log('props changed: ', nextProps)
		this.setState({AllExercises: nextProps.AllExercises, AllWorkouts: nextProps.AllWorkouts})	
	}
	/*Create Exercise Starts*/
	createExercise=(exerciseDetails)=>
	{	exerciseDetails.preventDefault();
		var fileSelect = document.getElementById('myfile');
		const { exerciseDescription, exerciseTitle, exerciseIntensity, exerciseType } = this.refs;
		if(exerciseType.value==""||exerciseDescription.value==""||exerciseTitle.value==""||exerciseIntensity.value==""||fileSelect.files.length==0)
		{
			alert("check all fields")
		}
		else
		{
			/* video upload */
			console.log("video uploading")
			var fileSelect = document.getElementById('myfile');
			var files = fileSelect.files;
			var formData = new FormData();
			var file = files[0];
			console.log(file);
			formData.append('myfile', file, file.name);
			console.log(file,formData)
			axios.post(APIs.UploadVideo,formData)
			.then((response)=>{
				this.setState({video_url:response.data.url},()=>{

					console.log('video uri: ', this.state.video_url);
					axios.post(APIs.CreateExercise,{
						"id": this.state.userID,
						"description":  exerciseDescription.value,
						"duration":  3,
						"title": exerciseTitle.value,
						"type":  exerciseType.value,
						"uri": this.state.video_url,
						"intensity":  exerciseIntensity.value,
					}).then((response)=>{
						alert(response.statusText);
						exerciseDescription.value="";
						exerciseTitle.value="";
						exerciseType.value="";
						exerciseIntensity.value="";
						this.props.callAllExercises();
					}).catch(error => {
						console.log(error)
					});
				});
			});
		}
	}
	/*Create Exercise Ends*/

	/*Create Workout Starts*/
	selectExerciseChanged = (newExercise) =>{
		this.setState({selectedExercise:newExercise})
	}

	createWorkout=(e)=>{
		const {  workoutTitle, workoutIntensity, workoutDescription } = this.refs;
		e.preventDefault();
		if(workoutDescription.value==""||workoutTitle.value==""||workoutIntensity.value==""||this.state.selectedExercise.length==0)
		{
			alert("check all fields")
		}
		else
		{
			axios.post(APIs.CreateWorkout,{
		      "description":workoutDescription.value,
		      "id":localStorage.getItem("id")/**/,
		      "duration":5,
		      "title":workoutTitle.value,
		      "intensity":workoutIntensity.value,
		      "list":this.state.selectedExercise/*list:[1,2,3]*/
		    })
		    .then((response)=>{
		    	this.setState({selectedExercise:[]})
		    	console.log(response.data);
		    	workoutDescription.value="";
				workoutTitle.value="";
				workoutIntensity.value="";
		    	this.props.callAllWorkouts();
		    	alert("workout created")
		    });
		}
	    
	}
	/*Create Workout Ends*/

	/*Create Plan Starts*/
	selectWorkoutChanged = (newWorkout) =>{
		this.setState({selectedWorkout:newWorkout})
	}

	createPlan=(e)=>{
		e.preventDefault();
		const { planDescription, planDuration, planTitle, planIntensity } = this.refs;
		if(planDuration.value==""||planDescription.value==""||planTitle.value==""||planIntensity.value==""||this.state.selectedWorkout.length==0){
			alert("check all fields")
		}
		else
		{
			axios.post(APIs.CreatePlan,{
		      "description":planDescription.value,
		      "id":localStorage.getItem("id")/**/,
		      "duration":planDuration.value,
		      "title":planTitle.value,
		      "intensity":planIntensity.value,
		      "workouts":this.state.selectedWorkout/*list:[1,2,3]*/
		    })
		    .then((response)=>{
		    	this.setState({selectedWorkout:[]})
		    	console.log(response.data);
		    	planDescription.value="";
				planDuration.value="";
				planTitle.value="";
				planIntensity.value="";
		    	this.props.callAllPlans();
		    	alert("plan created");
		    });
		}
	}
	/*Create Plan Ends*/
	render () {
		const { AllExercises, AllWorkouts } = this.state;
		$(document).ready(function() {
			$('input[type="radio"]').click(function()/*Switch between create modules*/
			{
	            var inputValue = $(this).attr("value");
	            var targetBox = $("." + inputValue);
	            $(".create-box1").not(targetBox).hide();
	            $(targetBox).show();
	             if($(this).attr("value") == "create-exercise"){
	              $('.rgt_frm_img1').show();
	            }
	            if($(this).attr("value") == "create-workout"){
	              $('.rgt_frm_img1').hide();
	            }
	            if($(this).attr("value") == "create-plan"){
	              $('.rgt_frm_img1').hide();
	            }
	             
	        });
	        $('#myButton').click(function() {
			   $.scrollTo($('#myDiv'), 1000);
			});
	    });
		return (
			<div>
				<div className="col-md-8 col-lg-12">
		            <h4 className="createItems1"><input type="radio" name="create-show" value="create-exercise"/> Create Exercise</h4>&nbsp;&nbsp;
		            <h4 className="createItems1"><input type="radio" name="create-show" value="create-workout"/> Create Workout</h4>&nbsp;&nbsp;
		            <h4 className="createItems1"><input type="radio" name="create-show" value="create-plan"/> Create Plan</h4>&nbsp;
		      	</div>
		      	<div  className="col-md-5 col-lg-5 col-xs-12 createCourse create-box1 create-exercise">
					<form >
						<div className="form-group">
							<label>Title</label>
							<input type="text" placeholder="Title" ref="exerciseTitle"/>
						</div>
						<div className="form-group">
							<label>Type</label>
							<input type="text" placeholder="Type" ref="exerciseType"/>
						</div>
						<div className="form-group">
							<label>Intensity</label>
							<input type="text" placeholder="Intensity (1-10)" ref="exerciseIntensity"/>
						</div>
						<div className="form-group">
							<label>Description</label>
							<input type="text" placeholder="Description" ref="exerciseDescription"/>
						</div>
						<div className="form-group">
							<label>File input</label>
							<input id="myfile" name="myFile" encType="multipart/form-data" type="file"/>
						</div>
						<ul id="fileList">
						</ul>
						<button className="btn btn-info" type="submit" onClick={(e)=>this.createExercise(e)}>Create Exercise</button>
					</form>
				</div>
				<div  className="col-md-12 col-lg-12 col-xs-12 createCourse create-box1 create-workout">
					{this.state.AllExercises.length?
					<div>
					<div  className="col-md-5 col-lg-5 col-xs-12 createCourse create-box1 create-workout">
						<form role="form" >
							<div className="form-group">
								<label>Title</label>
								<input type="text" placeholder="Title" name="Title" ref="workoutTitle" className="form-control" />
							</div>
							<div className="form-group">
								<label>Description</label>
								<input type="text" placeholder="Description" name="Description" ref="workoutDescription" className="form-control" />
							</div>
							<div className="form-group">
								<label>Intensity</label>
								<input type="text" placeholder="Intensity (1-10)" name="Intensity" ref="workoutIntensity" className="form-control" />
							</div>
							<button className="btn btn-info" onClick={(e)=>this.createWorkout(e)}>Create Workout</button>
						</form>
					</div>
					<ul className="wrkoutulli">
						<CheckboxGroup
				        name="exercises"
				        value={this.state.selectedExercise}
				        onChange={this.selectExerciseChanged}
				        >
				        {this.state.AllExercises.map((exercise, i)=>
							<li key={i}>
								<Checkbox value={exercise.id} className="inputchk4wrkout" />
								<ShowExercise exercise={exercise}/>
							</li>
						)}
						</CheckboxGroup>
					</ul>
					</div>:<h2 className="nocsv">Currently no exercises available please create exercise !</h2>}
				</div>
				<div  className="col-md-12 col-lg-12 col-xs-12 createCourse create-box1 create-plan">
					{this.state.AllWorkouts.length?
					<div> 
					<div  className="col-md-5 col-lg-5 col-xs-12 createCourse create-box1 create-plan">
						<form role="form" >
							<div className="form-group">
								<label>Title</label>
								<input type="text" placeholder="Title" name="Title" ref="planTitle" className="form-control" />
							</div>
							<div className="form-group">
								<label>Duration</label>
								<input type="text" placeholder="Duration (In days 1-30)" name="Duration" ref="planDuration" className="form-control" />
							</div>
							<div className="form-group">
								<label>Description</label>
								<input type="text" placeholder="Description" name="Description" ref="planDescription" className="form-control" />
							</div>
							<div className="form-group">
								<label>Intensity</label>
								<input type="text" placeholder="Intensity (1-10)" name="Intensity" ref="planIntensity" className="form-control" />
							</div>
							<button className="btn btn-info" onClick={(e)=>this.createPlan(e)}>Create Plan</button>
						</form>
					</div> 

					<ul className="wrkoutulli_1">
						<CheckboxGroup
				        name="workouts"
				        value={this.state.selectedWorkout}
				        onChange={this.selectWorkoutChanged}
				        >
				        {this.state.AllWorkouts.map((workout, i)=>
							<li key={i}>
								<Checkbox value={workout.id} className="inputchk4wrkout" />
								<ShowWorkout workout={workout} />
							</li>
						)}
						</CheckboxGroup>
					</ul>
					</div>:<h2 className="nocsv">Currently no workouts available please create workout !</h2>}
				</div>
		      	<div className="col-md-6 col-lg-6 col-xs-12 rgt_frm_img1">
					<img src="images/create_exercise_img.png" />
					<div className="beaches-banner-content">
						<h4>Do you love Exercise?</h4>
						<p>Whedamet, consectetur adipiscing elit. Duis pharetra varius quam sit amet experience in the Caribbean for over 15 years!.</p>
			            
					</div>
				</div>
			</div>
		)
	}
}
export default CreateAll;




